// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-window {
    $this: &;

    &__chat-window-container {
        height: 406px;
    }

    &__messages-container {
        @include eureka-scroll-y;
    }

    &__chat-text-container {
        @include eureka-flex-column;
        height: 100%;
    }

    .eureka-tile__content-container-inner {
        height: 100%;
    }

    &__send-message-container {
        margin-top: auto;
        
        ::-webkit-scrollbar {
            width: 0;
        }
          
        ::-webkit-scrollbar-track {
            opacity: 0;
        }
          
        ::-webkit-scrollbar-thumb {
            opacity: 0;
        }
    }

    &__counter-container {
        font-size: var(--eureka-core-typography-footnote-font-size, 1.1);
        margin-bottom: 5px;
        color: var(--eureka-core-color-neutral-40)
    }

    &__actions-container {
        @include eureka-flex-row;
        #{$this}__attach-btn {
            color:  var(--eureka-btn-primary-color-border);
        }

        #{$this}__send-btn {
            width: 25px;
            height: 25px;
            border-radius: var(--eureka-chat-floating-btn-border-radius);
            margin: auto;

            .eureka-button__icon-safe-space {
                display: inline-flex;
                vertical-align: unset;
            }
        }

        .eureka-button__content-container, 
        .eureka-button__icon-container {
            padding: 0;
        }
    }

    &__attach-btn {
        margin-right: 10px;
    }

    &__after-control-container {
        @include eureka-flex-column;
        justify-content: flex-end;
        -ms-flex: 0 0 auto;
        flex: 0 0 auto;
        align-items: flex-end;
        position: absolute;
        bottom: 0;
        right: 0;
        margin-right: 3px;
        margin-bottom: 4px;
    }
}
