// choose option window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.choose-option-window {
    $this: &;
    
    &__header-container {
       margin-bottom: 16px;
       padding: 0;

       .eureka-callout__title {
            font-size: var(--eureka-core-typography-headings-level5-font-size);
            letter-spacing: -0.03em;
       }

       .eureka-callout__inner {
            text-align: left;
       }

       .eureka-list, %eureka-list {
            @include eureka-list-reset;
    
            &__li + &__li {
                border-top: var(--eureka-list-border-width, eureka-light-theme('list-border-width')) solid var(--eureka-core-color-universal-border, eureka-light-theme('core-color-universal-border'));
            }
        }
    }

    &__content-container {
        border: var(--eureka-list-border-width, eureka-light-theme('list-border-width')) solid var(--eureka-core-color-universal-border, eureka-light-theme('core-color-universal-border'));
        border-radius: var(--eureka-chat-window-border-radius);

        .eureka-list-item {
            background-color: unset;
        }
    }

    &__svg-container {
        margin-top: 3px;
    }
}
