@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '../../../styles/chat-module.scss';

// modal scss
.modal-wrapper {
    box-shadow: 0 0 20px rgba(0, 0, 0, 40%);
    border-radius: var(--eureka-modal-dialog-border-radius, 20px);
    text-align: center;

    &__overlay.eureka-modal__overlay {
        @include eureka-flex-column;
        align-items: center;
        background-color: var(--eureka-modal-overlay-color-bg,
                rgba(0, 0, 0, 50%));
        border-radius: var(--eureka-tile-border-radius);
        cursor: initial;
        max-width: 376px;
        bottom: 0;
        right: 0;
        left: unset;
        top: unset;
        position: unset;

        .eureka-modal {
            height: auto;
            max-width: 343px;

            &__main-container {
                padding-bottom: 0;
            }

            &__actions-container-inner {
                margin: 0;
            }

            &__actions-container {
                justify-content: center;
                margin-top: 24px;
            }
        }
    }

    .eureka-callout {
        padding: 0;
    }

    .eureka-icon {
        width: 53px;
        height: 53px;
    }

    &__icon--primary .eureka-icon {
        color: var(--eureka-core-color-primary);
    }

    &__icon--error .eureka-icon {
        color: var(--eureka-core-color-destructive);
    }

    .eureka-callout__title-container .eureka-heading--h2 {
        font-size: var(--eureka-core-typography-headings-level5-font-size);
    }

    &__btn--single.modal-wrapper__btn.eureka-button {
        width: 311px;
    }

    .eureka-button {
        &--primary {
            width: 140px;
        }

        &--secondary {
            color: var(--eureka-core-color-primary);
            width: 140px;
        }

        &__content-container {
            padding: 0.8rem 0 0.9rem;
        }

        &__text-container {
            padding: 0;
        }
    }
}

.react-modal-portal {
    z-index: 999;
    position: fixed;
    bottom: 0;
    width: 100%;
    max-width: 376px;
}

@include eureka-breakpoint-iphone() {
    .react-modal-portal,
    .modal-wrapper__overlay.eureka-modal__overlay {
        max-width: $iphone-chat-full-screen-max-width;
        border-radius: 0px;
    }
}

@include eureka-breakpoint-iphone(landscape) {
    .react-modal-portal,
    .modal-wrapper__overlay.eureka-modal__overlay {
        max-width: $iphone-chat-full-screen-max-height;
        border-radius: 0px;
    }
}