// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-image-bubble {
    margin: 8px 5px;
    $this: &;

    &__loading-container {
        max-width: var(--chat-bubble-max-width);
        font-size: var(--eureka-core-typography-footnote-font-size);
        border-radius: var(--eureka-chat-bubble-border-radius);
        padding: 0.8rem 1.2rem;
        text-align: center;
        position: relative;

        #{$this}__loading-bubble {
            display: flex;
            flex-direction: row;
            background-color: var(--eureka-core-color-canvas-alt);
            color: var(--eureka-core-color-text);
            width: 95px;
            height: 56px;
            align-items: center;
            justify-content: center;
            border-radius: var(--eureka-chat-bubble-border-radius);
            position: relative;

            .web-chat-svg-image svg {
                width: 33px;
                height: 26px;
            }

            #{$this}__tail {
                position: absolute;
                display: block;
                width: 12px;
                height: 15px;
                bottom: 0;
            }
        }

        #{$this}__uploading-status {
            font-size: var(--eureka-core-typography-footnote-font-size);
            color: var(--eureka-core-color-neutral-40);
        }
    }

    &__bubble-thumbnail-container {
        max-width: 100px;
        max-height: 133px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        border-radius: var(--eureka-chat-bubble-border-radius);
        box-shadow: 0 0 0 0.2rem transparent;

        #{$this}__thumbnail-container,
        #{$this}____thumbnail {
            border-radius: var(--eureka-chat-bubble-border-radius);
        }

        #{$this}__modal-scrim {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            min-height: 6rem;
            border-radius: var(--eureka-chat-bubble-border-radius);
            position: absolute;
            top: 0;
            color: transparent;
            text-align: center;
        }

        #{$this}__modal-scrim:hover {
            color: var(--eureka-core-color-canvas);
            background-color: var(--eureka-core-color-universal-focus-halo);
        }
    }

    &__bubble-thumbnail-container:focus-visible {
        outline-color: transparent;
        outline-width: 0.2rem;
        outline-style: solid;
        box-shadow: 0 0 0 0.2rem var(--eureka-tabs-active-focus-halo-color);
        border-radius: var(--eureka-chat-bubble-border-radius);

        #{$this}__modal-scrim {
            color: var(--eureka-core-color-canvas);
            background-color: var(--eureka-core-color-universal-focus-halo);
        }
    }

    &__thumbnail-container {
        @include eureka-flex-column;
        min-height: 60px;
        justify-content: center;
        border-radius: var(--eureka-chat-bubble-border-radius);
    }

    &--from-me {
        @include eureka-flex-row-reverse;

        #{$this}__bubble-loading-container {
            background-color: var(--eureka-core-color-primary);
            color: var(--eureka-core-color-canvas);
        }

        #{$this}__loading-bubble {
            background-color: var(--eureka-core-color-canvas);
        }

        #{$this}__tail {
            right: -6px;
            color: var(--eureka-core-color-primary);
        }
    }

    &--from-other {
        @include eureka-flex-row;

        #{$this}__loading-bubble {
            color: var(--eureka-core-color-neutral-40);
        }

        #{$this}__tail {
            left: -5px;
            color: var(--eureka-core-color-canvas-alt);
        }
    }
}
