// chat window scss
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-text-bubble {
    $this: &;

    &__bubble-actions-container {
        margin: 8px 5px;
    }

    &__bubble-actions-container::after {
        content: '';
        clear: both;
        display: table;
    }

    &__bubble-container {
        max-width: var(--chat-bubble-max-width);
        border-radius: var(--eureka-chat-bubble-border-radius);
        padding: 0.8rem 1.2rem;
        position: relative;
        word-break: break-word;
        white-space: pre-line;
    }

    &__tail {
        position: absolute;
        display: block;
        width: 12px;
        height: 15px;
        bottom: 0;
    }

    &--from-me {
        @include eureka-flex-row-reverse;

        #{$this}__bubble-container {
            background-color: var(--eureka-core-color-primary);
            color: var(--eureka-core-color-canvas);
        }

        #{$this}__tail {
            right: -6px;
            color: var(--eureka-core-color-primary);
        }
    }

    &--from-other {
        @include eureka-flex-row;

        #{$this}__bubble-container {
            background-color: var(--eureka-core-color-canvas-alt);
            color: var(--eureka-core-color-text);
        }

        #{$this}__tail {
            left: -5px;
            color: var(--eureka-core-color-canvas-alt);
        }
    }

    &__actions-container {
        float: right;
        overflow: auto;
        font-weight: var(--eureka-core-typography-font-weight);
        font-size: var(--eureka-core-typography-footnote-font-size, 11);

        #{$this}__actions-text {
            color: var(--eureka-core-color-error);
        }

        #{$this}__actions-link-retry {
            margin-left: 7px;
        }

        #{$this}__actions-divider {
            margin-left: 2.5px;
            margin-right: 2.5px;
        }
    }
}
