@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';

.web-chat-window {
    .textarea--autosize {
        width: 80%;
        line-height: 2.5rem;
        padding: .4rem .3rem .2rem 1.2rem;
        resize: none;
        caret-color: var(--eureka-form-input-color-text);
        -ms-overflow-style: none; /* IE 11 */
        scrollbar-width: none; /* Firefox 64 */
    }

    .textarea--autosize:focus {
        outline: none;
    }

    .eureka-floating-label-composer__after-comp-container{
        height: 2.5rem;
        margin: 5px;
    }

    .eureka-floating-label-composer {
        display: block;
    }
}