// icon button chat scss

.icon-button-chat {
    $this: &;
    position: absolute;
    right: 25px;
    width: 64px;
    &__container {
        position: fixed;
        z-index: 999999;
        bottom: 25px;
    }

    &__icon-btn-chat.eureka-button {
        width: 64px;
        height: 64px;
        border-radius: var(--eureka-chat-floating-btn-border-radius);
        border-color: var(--eureka-chat-floating-btn-color-border);
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
        color: var(--eureka-core-color-primary);
        font-size: var(--eureka-core-typography-sub-text-font-size);
    }

    &__icon-btn-chat.eureka-button:hover {
        border-color: var(--eureka-chat-floating-btn-hover-color-border);
    }

    .eureka-button__icon-container, .eureka-button__text-container {
        padding: 0;
    }

    .eureka-button__icon-safe-space {
        width: auto;
    }

    &__count {
        position: absolute;
        top: 0;
        right: 4px;
        width: 14px;
        height: 14px;
        text-align: center;
        border-radius: 100%;
        background: var(--eureka-core-color-destructive);
    }

    &__count[data-count="0"] {
        display: none;
    }

    &__count-number {
        width: 12px;
        height: 12px;
    }
}
