.web-chat-leave-message {
    $this: &;

    .eureka-tile__content-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__leave-message-content {
        .eureka-callout {
            padding: 0;
            margin-bottom: 12px;

            &__title {
                font-size: var(
                    --eureka-core-typography-headings-level5-font-size
                );
            }
        }

        // TODO: will be replaced by Eureka UI Form field composer
        #{$this}__input {
            width: 100%;
            margin-bottom: 16px;
            padding: 1rem;
            border-radius: var(--eureka-form-input-border-radius);
        }

        // TODO: will be replaced by Eureka UI Form field composer
        #{$this}__textarea {
            resize: none;
            width: 100%;
            padding: 1rem;
            margin-bottom: 12px;
            border-radius: var(--eureka-form-input-border-radius);
        }

        // TODO: will be replaced by Eureka UI Form field composer
        #{$this}__textarea:focus {
            outline: none;
        }

        #{$this}__errorMsg {
            font-size: var(--eureka-core-typography-footnote-font-size);
            text-align: center;
            margin-top: 0;
            color: var(--eureka-core-color-error);
        }

        #{$this}__sendMsgBtn {
            width: 100%;
        }
    }
}
